import Vue from 'vue';
import VueRouter from 'vue-router';
import ReportTemplate from '../components/ReportTemplate.vue';
import Upload from '../components/SQLTemplate.vue';
import AddSchedule from '../components/ScheduleDetail.vue';
import Authentication from '../components/Authentication.vue';
import Error from '../components/Error.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'mainPage',
    component: AddSchedule,
    meta: {title: 'AddSchedule'},
  },
  {
    path: '/ReportTemplate',
    name: 'ReportTemplate',
    component: ReportTemplate,
    meta: {title: 'ReportTemplate'},
  },
  {
    path: '/upload',
    name: 'upload',
    component: Upload,
    meta: {title: 'Upload'},
  },
  {
    path: '/addSchedule',
    name: 'addSchedule',
    component: AddSchedule,
    meta: {title: 'AddSchedule'},
  },
  {
    path: '/authentication',
    name: 'authentication',
    component: Authentication,
    meta: {title: 'Authentication'},
  },
  {
    path: '/error',
    name: 'error',
    component: Error,
    meta: {title: 'Error'},
  }
];

const router = new VueRouter({mode: 'hash', routes});

export default router;



