import Vue from 'vue';

const findAllSchedule = function () {
  return new Promise(resolve => {
    const url = '/api/fcrt/reportSchedule/findAll';
    Vue.http.get(url)
      .then(function (res) {
        resolve(res.body.data);
      })
      .catch(function (err) {
        console.error('can not init schedule page' + err.body.message);
        resolve([]);
      });
  });
};

const deleteSchedule = function (scheduleId) {
  return new Promise(resolve => {
    const url = '/api/fcrt/reportSchedule/delete/' + scheduleId;
    Vue.http.delete(url)
      .then(function (res) {
        resolve(res.body.data);
      })
      .catch(function (err) {
        console.error('delete schedule fail' + err.body.message);
        resolve([]);
      });
  });
};

const addSchedule = function (schedule) {
  return new Promise(resolve => {
    const url = '/api/fcrt/reportSchedule/save';
    Vue.http.post(url, schedule)
      .then(function (res) {
        resolve(res.body.data);
      })
      .catch(function (err) {
        console.error('upload schedule fail:' + err.body.message);
        resolve([]);
      });
  });
};

const findAllTemplate = function () {
  return new Promise(resolve => {
    const url = '/api/fcrt/reportTemplate/findAll';
    Vue.http.get(url)
      .then(function (res) {
        resolve(res.body.data);
      })
      .catch(function (err) {
        console.error('can not init schedule page' + err.body.message);
        resolve([]);
      });
  });
};

const triggerSchedule = function (scheduleId) {
  let self = this;
  return new Promise(resolve => {
    const url = '/api/fcrt/reportSchedule/triggerScheduleManually/' + scheduleId;
    Vue.http.get(url)
      .then(function (res) {
        resolve(res.body.data);
      })
      .catch(function (err) {
        resolve(err);
      });
  });
};

export default {
  findAllSchedule: findAllSchedule,
  deleteSchedule: deleteSchedule,
  addSchedule: addSchedule,
  findAllTemplate: findAllTemplate,
  triggerSchedule: triggerSchedule,
};



