import 'babel-polyfill';
import Vue from 'vue';
import App from './App';

import router from './router';
import vueResource from 'vue-resource';
import 'element-ui/lib/theme-chalk/index.css';
import ElementUI from 'element-ui';
import lang from 'element-ui/lib/locale/lang/en';
import locale from 'element-ui/lib/locale';
import CookiesHelper from './utilities/cookiesHelper';

locale.use(lang);
Vue.use(vueResource);
Vue.use(ElementUI);

Vue.config.productionTip = false;

Vue.http.interceptors.push((request, next)  =>{

  let adaptorName;
  let expirationTime;
  let integrityIdentifier;
  let type;
  let uniqueIdentifier;
  let user;

  if(request.getUrl().search('/authentication/login') != -1 ) {
    adaptorName = sessionStorage.getItem('adaptorName');
    expirationTime = sessionStorage.getItem('expirationTime');
    integrityIdentifier = sessionStorage.getItem('integrityIdentifier');
    type = sessionStorage.getItem('type');
    uniqueIdentifier = sessionStorage.getItem('uniqueIdentifier');
    user = sessionStorage.getItem('user');
  }else {
    adaptorName = CookiesHelper.get('adaptorName');
    expirationTime = CookiesHelper.get('expirationTime');
    integrityIdentifier = CookiesHelper.get('integrityIdentifier');
    type = CookiesHelper.get('type');
    uniqueIdentifier = CookiesHelper.get('uniqueIdentifier');
    user = CookiesHelper.get('user');
  }

  if(adaptorName){
    request.headers.set('adaptorName',adaptorName);
    request.headers.set('expirationTime',expirationTime);
    request.headers.set('integrityIdentifier',integrityIdentifier);
    request.headers.set('type',type);
    request.headers.set('uniqueIdentifier',uniqueIdentifier);
    request.headers.set('user',user);
  } next((response) => {
    if (response.status === 401) {
      router.push({name: 'error'});
    }
    return response;
  });
});


/* eslint-disable no-new */
const vue = new Vue({
  el: '#root',
  template: '<App/>',
  components: {App},
  router,
});

import CanvasNest from 'canvas-nest.js';
const config = {
  count: 88,
};

const cn = new CanvasNest(vue.$el, config);



