const isNullOrEmpty = (str) => {
  return (str == null || str == '') ? true : false;
};

const validateIsUnique = (value, arr, attr) => {
  let result = true;
  arr.forEach(function (item) {
    if (item['id']!=value['id'] && item[attr] === value[attr]) {
      result = false;
    }
  });
  return result;
};

export default {
  isNullOrEmpty: isNullOrEmpty,
  validateIsUnique: validateIsUnique,
};



