<template>
  <div></div>
</template>

<script>
  import AthenticationService from '../services/AuthenticationService';
  import router from '../router';

  export default {
    name: 'Authentication',
    methods: {
      handleUrl () {
        sessionStorage.setItem("adaptorName", this.$route.query.adaptorName);
        sessionStorage.setItem("expirationTime", this.$route.query.expirationTime);
        sessionStorage.setItem("integrityIdentifier", this.$route.query.integrityIdentifier);
        sessionStorage.setItem("type", this.$route.query.type);
        sessionStorage.setItem("uniqueIdentifier", this.$route.query.uniqueIdentifier);
        sessionStorage.setItem("user", this.$route.query.user);
      },
      async auth() {
        let data = await AthenticationService.login();
        console.log(data)
      }
    },
    created () {
      this.handleUrl();
      this.auth();
    }
  };
</script>

<style>
</style>



