import Vue from 'vue';
import router from '@/router';

const login = function () {
  return new Promise(resolve => {
    const url = '/api/fcrt/authentication/login';
    Vue.http.get(url)
      .then(function (res) {
        resolve(res.body.data);
      });
  });
};

export default {
  login: login
};



