<template>
  <div class='report-template tab-style'>
    <div class='tab-header-name'>
      <span>Report Template Maintenance</span>
      <el-button type='primary' id='add-button' plain @click='add' class="add-button">
        New Template
      </el-button>
    </div>
    <el-table
      :data='reportTemplates'
      style='width: 100%;height: 85%; overflow-y: auto;background-color:transparent'
      :row-style="rowStyle"
      :header-cell-style="headerStyle"
      highlight-current-row>
      <el-table-column
        type='index'
        label='#'
        width='40'>
      </el-table-column>
      <el-table-column
        sortable
        prop='name'
        label='Name'
        width='180'>
      </el-table-column>
      <el-table-column
        prop='description'
        label='Description'>
      </el-table-column>
      <el-table-column label='Action' width='300'>
        <template slot-scope='scope'>
          <el-button size='mini' type='info' plain @click='view(scope.$index, scope.row)'>
            View
          </el-button>
          <el-button size='mini' type='primary' plain @click='modify(scope.$index, scope.row)'>
            Edit
          </el-button>
          <el-button size='mini' type='danger' plain @click='remove(scope.$index, scope.row)'>
            Delete
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-container>
      <div class="pagination" style='margin: 0 auto;'>
        <el-pagination layout='prev, pager, next' :total='totalElements' :page-size='limit' background
                       @current-change='onPageChange'>
        </el-pagination>
      </div>
    </el-container>
    <report-detail :showDetail='showDetail' :templateId='templateId'
                   :isNew='isNew' :isEditable='isEditable'
                   @updateReport='updateReport'
                   @updateShowState='updateShowState'></report-detail>
  </div>
</template>

<script>
  import ReportDetail from './ReportDetail';
  import ReportTemplateService from '../services/ReportTemplateService';
  import Constants from '../utilities/constants';

  export default {
    components: {
      ReportDetail,
    },
    data () {
      return {
        showDetail: false,
        isNew: false,
        isEditable: false,
        templateId: '',
        reportTemplates: [],
        limit: Constants.PAGE_LIMIT,
        totalElements: 0,
        currentPage: 1,
      };
    },
    async mounted () {
      await this.getRequestFormDate(1);
    },
    methods: {
      headerStyle(){
        return 'background-color:#F6F6F6;font-size:16px;';
      },
      rowStyle(){
        return 'background-color:transparent';
      },
      add () {
        this.showDetail = true;
        this.isNew = true;
        this.isEditable = true;
      },
      modify (index, row) {
        this.templateId = row.id;
        this.showDetail = true;
        this.isNew = false;
        this.isEditable = true;
      },
      view (index, row) {
        this.templateId = row.id;
        this.showDetail = true;
        this.isNew = false;
        this.isEditable = false;
      },
      remove (index, row) {
        let templateId = row.id;
        this.$confirm('Are you sure to delete?', 'Warning', {
          confirmButtonText: 'Yes',
          cancelButtonText: 'No',
          type: 'warning',
        }).then(async () => {
          let scheduleList = await ReportTemplateService.findReportSchedulesByTmplId(templateId);
          if (scheduleList && scheduleList.length > 0) {
            let scheduleNameLists = scheduleList.map(function (item) {
              return item.name;
            }).join('; ');
            let h = this.$createElement;
            this.$alert(
              h('div', null, [
                h('p', null, 'The template is referenced by the schedules below'),
                h('p', {style: 'font-weight:bold;text-align:center'}, scheduleNameLists),
                h('p', null, '  please confirm no referenced before delete'),
              ]), 'Template delete error', {
                confirmButtonText: 'confirm',
                callback: action => {
                  this.$message({
                    type: 'info',
                    message: `action: ${ action }`,
                  });
                },
              });
            return;
          };
          await ReportTemplateService.deleteReportTemplate(templateId).catch(() => {
            this.$message({
              type: 'error',
              message: 'delete fail!',
            });
          });
          this.getRequestFormDate(this.currentPage);
          this.$message({
            type: 'success',
            message: 'delete successfully!',
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: 'cancel delete'
          });
        });
      },
      async updateReport (updatedReport) {
        const reportTemplate = this.buildReportTemplate(updatedReport);
        await ReportTemplateService.saveOrUpdateReportTemplates(
          reportTemplate).catch(() => {
          alert('save or update report template fail!');
        });
        this.getRequestFormDate(this.currentPage);
      },
      buildReportTemplate (updatedReport) {
        const reportTemplate = {
          reportTemplate: {
            description: updatedReport.description,
            isActive: updatedReport.isActive ? '1' : '0',
            name: updatedReport.name,
          },
        };
        if (updatedReport.id) {
          reportTemplate.reportTemplate.id = updatedReport.id;
        }
        reportTemplate.reportTmplScriptAssnList = [];
        if (updatedReport.selectedScripts && updatedReport.selectedScripts.length > 0) {
          updatedReport.selectedScripts.forEach(
            selectedScript => {
              reportTemplate.reportTmplScriptAssnList.push({
                scriptId: selectedScript.id,
                scriptExecuteSequence: selectedScript.scriptExecuteSequence,
                sheetName: selectedScript.sheetName,
              });
            });
        }
        return reportTemplate;
      },
      async getRequestFormDate (page) {
        let sort = {
          property: 'id',
          direction: 'DESC',
        };
        let response = await ReportTemplateService.findAllReportTemplate({
          page: page,
          start: 0,
          limit: this.limit,
          sortOrderList: [sort],
        });
        this.reportTemplates = response.content;
        this.totalElements = response.totalElements;
      },
      onPageChange (page) {
        this.getRequestFormDate(page);
        this.currentPage = page;
      },
      updateShowState (state) {
        this.templateId = undefined;
        this.showDetail = state;
      },
    },
  };
</script>

<style lang='less'>
  .table-body {
    margin: 80px auto;
    width: 80%;
    background-color: white;
    border: 1px solid;
  }

</style>



