<template>
  <div class="tab-style">
    <div class='tab-header-name'>
    <span>SQL Maintenance</span>
    <el-button size='large' type='primary' plain @click='showUploadForm()' class='add-button'>
      New Script File
    </el-button>
    </div>
    <el-table
      :data='tableData'
      style='width: 100%;height: 85%; overflow-y: auto;background-color:transparent'
      :row-style="rowStyle"
      :header-cell-style="headerStyle"
      highlight-current-row
      :default-sort="{prop: 'createDate', order: 'descending'}">
      <el-table-column
        type='index'
        label='#'
        width='40'>
      </el-table-column>
      <el-table-column
        prop='name'
        label='Name'
        sortable
        width='180'>
      </el-table-column>
      <el-table-column
        prop='description'
        label='Description'
        sortable>
      </el-table-column>
      <el-table-column
        prop='sqlContent'
        label='SQL Content'
        sortable>
        <template slot-scope='scope'>
          <el-button size='mini' type='text' @click='onViewSqlContent(scope.row.content)'>view Sql Content</el-button>
        </template>
      </el-table-column>
      <el-table-column
        prop='createBy'
        label='Create By'
        sortable>
      </el-table-column>
      <el-table-column
        prop='createDate'
        label='Create Date'
        :formatter='formatDate'
        sortable>
      </el-table-column>
      <el-table-column
        prop='updateBy'
        label='Update By'
        width='180'
        sortable>
      </el-table-column>
      <el-table-column
        prop='updateDate'
        label='Update Date'
        :formatter='formatDate'
        width='180'
        sortable>
      </el-table-column>
      <el-table-column label='Action' width='300'>
        <template slot-scope='scope'>
          <el-button size='mini' type='primary' plain @click='onEditSql(scope.row)'>Edit</el-button>
          <el-button size='mini' type='danger' plain @click='onDeleteSqlInfo(scope.row)'>Delete</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-container>
      <div class="pagination" style='margin: 0 auto;'>
        <el-pagination layout='prev, pager, next' :total='totalElements' :page-size='limit' background
                       @current-change='onPageChange'>
        </el-pagination>
      </div>
    </el-container>
    <el-dialog title='Upload' width='35%' :visible.sync='upLoadVisible'>
      <el-form :model='sqlInfo' ref='uploadForm' :rules='uploadRules'>
        <el-form-item label='Name' prop='name' label-width='110px'>
          <el-input v-model='sqlInfo.name' maxlength='50' class='detail-input'></el-input>
        </el-form-item>
        <el-form-item label='Description' prop='description' label-width='110px'>
          <el-input v-model='sqlInfo.description' type='textarea' :rows='3' class='detail-input'></el-input>
        </el-form-item>
        <el-form-item label='SqlFile' prop='sqlFile' label-width='110px'>
          <div>
            <el-upload
              class='upload-demo'
              :limit='1'
              action='/api/fcrt/reportScript/uploadFile'
              :on-change='onFileChange'
              :file-list='fileList'
              :auto-upload="false"
            >
              <el-button size='small' type='success'>upload</el-button>
            </el-upload>
            <div v-if="sqlContentValidate" class="el-form-item__error">{{sqlValidateResult}}</div>
          </div>
        </el-form-item>

        <el-form-item label='Script Type:' label-width='110px' style='width: 400px' prop='scriptType'>
          <el-radio v-model='sqlInfo.scriptType' label='preProcess'>preprocess</el-radio>
          <el-radio v-model='sqlInfo.scriptType' label='query'>query</el-radio>
        </el-form-item>
      </el-form>
      <div class='dialog-footer'>
        <el-button @click='onCancelUpload'>Cancel</el-button>
        <el-button type='primary' @click='submitSql("uploadForm")'>Submit</el-button>
      </div>
    </el-dialog>

    <el-dialog title='Update' width='35%' :visible.sync='updateVisible'>
      <el-form :model='sqlInfo' ref='updateForm' :rules='uploadRules'>
        <el-form-item label='Name' prop='name' label-width='110px'>
          <el-input v-model='sqlInfo.name' maxlength='50' class='detail-input'></el-input>
        </el-form-item>
        <el-form-item label='Description' prop='description' label-width='110px'>
          <el-input v-model='sqlInfo.description'  type='textarea' :rows='3' class='detail-input'></el-input>
        </el-form-item>
        <el-form-item label='Sql File' prop='sqlFile' label-width='110px'>
          <div>
            <el-upload
              class='upload-demo'
              :limit='1'
              action='/api/fcrt/reportScript/uploadFile'
              :on-change='onFileChange'
              :file-list='fileList'
              :auto-upload="false"
            >
              <el-button size='small' type='success'>upload</el-button>
            </el-upload>
            <div v-if="sqlContentValidate" class="el-form-item__error">{{sqlValidateResult}}</div>
          </div>
        </el-form-item>
        <el-form-item label='Script Type:' label-width='110px' style='width: 400px' prop='scriptType'>
          <el-radio v-model='sqlInfo.scriptType' label='preProcess'>preprocess</el-radio>
          <el-radio v-model='sqlInfo.scriptType' label='query'>query</el-radio>
        </el-form-item>
      </el-form>
      <div class='dialog-footer'>
        <el-button @click='onCancelUpdateSql'>Cancel</el-button>
        <el-button type='primary' @click='submitSql("updateForm")'>Update</el-button>
      </div>
    </el-dialog>

    <el-dialog
      title='Sql Content'
      :visible.sync='sqlContentVisible'
      width='60%'>
      <span class="sql-content">{{dialogSqlContent}}</span>
      <span slot='footer' class='dialog-footer'>
    <el-button type='primary' @click='sqlContentVisible = false'>OK</el-button>
  </span>
    </el-dialog>
  </div>

</template>

<script>
  import ReportScriptService from '@/services/ReportScriptService';
  import Constants from '../utilities/constants';
  import ValidateHelper from '../utilities/validateHelper';
  import * as dateHelper from '../utilities/dateHelper';

  export default {
    data () {
      return {
        tableData: [],
        uploadRules: {
          name: [{required: true, message: 'Please input Sql Name', trigger: 'blur'}],
          description: [{required: true, message: 'Please input Sql Description', trigger: 'blur'}],
          scriptType: [{required: true, message: 'Please select script type', trigger: 'blur'}],
        },
        sqlInfo: {},
        upLoadVisible: false,
        sqlContentVisible: false,
        dialogSqlContent: '',
        fileList: [],
        updateVisible: false,
        sqlContentValidate: false,
        totalElements: 0,
        limit: Constants.PAGE_LIMIT,
        currentPage: 1,
        sqlValidateResult: '',
      };
    },
    mounted () {
      this.getRequestFormDate(1);
    },
    methods: {
      headerStyle(){
        return 'background-color:#F6F6F6;font-size:16px;';
      },
      rowStyle(){
        return 'background-color:transparent';
      },
      formatDate(row, column, cellValue){
        return dateHelper.getFormatDateByPattern(cellValue,dateHelper.ISO_DATE_FORMAT,dateHelper.UI_DATE_FORMAT);
      },
      onCancelUpdateSql () {
        this.updateVisible = false;
        this.$refs.updateForm.resetFields();
        this.sqlInfo = {};
        this.fileList = [];
      },
      showUploadForm () {
        this.upLoadVisible = true;
      },
      onCancelUpload () {
        this.upLoadVisible = false;
        this.$refs.uploadForm.resetFields();
        this.sqlInfo = {};
        this.fileList = [];
      },
      onViewSqlContent (content) {
        this.sqlContentVisible = true;
        if (content === null || content === '') {
          this.dialogSqlContent = 'No Sql Content,Please update';
        } else {
          this.dialogSqlContent = content;
        }
      },
      onEditSql (sql) {
        this.updateVisible = true;
        this.sqlInfo = Object.assign({},sql);
      },
      onDeleteSqlInfo (row) {
        this.deleteSql(row);
      },
      submitSql (uploadForm) {
        this.$refs[uploadForm].validate(async valid => {
          let emptyValidateResult = ValidateHelper.isNullOrEmpty(this.sqlInfo.content);
          if (valid && !emptyValidateResult) {
              if (ValidateHelper.validateIsUnique(this.sqlInfo, this.tableData, 'name')) {
                let result = await ReportScriptService.upload(this.sqlInfo);
                if (result === null) {
                  return;
                }
                this.getRequestFormDate(this.currentPage);
                this.upLoadVisible = false;
                this.updateVisible = false;
                this.sqlInfo = {};
                this.fileList = [];
              } else {
                this.$message({
                  type: 'error',
                  message: 'Sql Name Duplicate',
                });
              }
          }
          else {
            if (emptyValidateResult) {
              this.sqlValidateResult = 'Please upload the sql file';
              this.sqlContentValidate = true;
            }
            return false;
          }
        });
      },
      onPageChange (page) {
        this.getRequestFormDate(page);
        this.currentPage = page;
      },
      onFileChange (file) {
        var _this = this;
        if (file.raw) {
          let reader = new FileReader();
          reader.onload = function (e) {
            _this.sqlInfo.content = e.target.result;
          };
          if (ValidateHelper.isNullOrEmpty(this.sqlInfo.content )) {
            this.sqlContentValidate = false;
          }
          reader.readAsText(file.raw);
        }
      },
      async getAllSql () {
        this.tableData = await ReportScriptService.findAllSql();
      },
      async getRequestFormDate (page) {
        let sort = {
          property: 'createDate',
          direction: 'ASC',
        };
        let response = await ReportScriptService.getRequestFormWithCondition({
          page: page,
          start: 0,
          limit: this.limit,
          sortOrderList: [sort],
        });
        this.tableData = response.content;
        this.totalElements = response.totalElements;
      },
      async deleteSql (sql) {
        this.$confirm(`Are you sure you want to delete this sql <strong> ${sql.name} </strong>?`, 'Warning', {
          dangerouslyUseHTMLString: true,
          confirmButtonText: 'Delete',
          cancelButtonText: 'Cancel',
          type: 'warning',
          callback: async action => {
            if (action === 'confirm') {
              let reportdLists = await ReportScriptService.fingTmplByScriptDetailId(sql);
              if (reportdLists && reportdLists.length > 0) {
                let reportdNameLists = reportdLists.map(function (item) {
                  return item.name;
                }).join('; ');
                let h = this.$createElement;
                this.$alert(
                  h('div', null, [
                    h('p', null, 'The SQL is referenced by the report template below:'),
                    h('p', {style: 'font-weight:bold;text-align:center'}, reportdNameLists),
                    h('p', null, 'please confirm no referenced before delete'),
                  ])
                  , 'SQL delete error', {
                    confirmButtonText: 'confirm',
                    callback: action => {
                      this.$message({
                        type: 'info',
                        message: `action: ${ action }`,
                      });
                    },
                  });
                return;
              }
              let result = await ReportScriptService.deleteSql(sql);
              if (result === null) {
                return;
              }
              this.getRequestFormDate(this.currentPage);
              this.$message({
                type: 'success',
                message: 'Delete successful.',
              });
            }
          },
        });
      },
    },
  }
  ;
</script>
<style scoped type="text/css">
  .sql-content {
    white-space: pre-wrap;
    word-wrap: break-word;
    word-break: break-all;
  }
</style>



