<template>
  <div id='app'>
    <div class="main">
      <el-menu default-active="/addSchedule" class="el-menu-demo" @select="handleSelect"
               background-color="#23103c"
               text-color="#6e6e6e"
               active-text-color="white">
        <el-menu-item index='/addSchedule' class="menu-project-name"><span>{{project_title}}</span>
        </el-menu-item>
        <el-menu-item index="/upload" style="font-weight: bold">SQL</el-menu-item>
        <el-menu-item index="/ReportTemplate" style="font-weight: bold">Template</el-menu-item>
        <el-menu-item index="/addSchedule" style="font-weight: bold">Schedule</el-menu-item>
      </el-menu>
      <router-view class='router-view'></router-view>
    </div>
  </div>
</template>

<script>
  import Constants from '@/utilities/constants';

  export default {
    data () {
      return {
        activeIndex: '1',
        project_title: Constants.PROJECT_TITLE,
      };
    },
    methods: {
      handleSelect (key, keyPath) {
        this.$router.push({path: keyPath[0]});
      },
    },
  };
</script>

<style lang='less'>

  html, body {
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
  }

  #app {
    height: 100%;
    width: 100%;
  }

  .add-button {
    float: right;
    margin: 0 20px 20px 0;
  }

  .el-menu-item-style {
    font-family: 'Verdana', 'Arial', 'Helvetica', 'sans-serif';
    font-weight: bold;
    font-size: 28px;
    color: #FFFFFf;
    /*line-height: 56px;*/
    /*height: 56px;*/
    vertical-align: top;
    display: flex;
  }

  .menu-project-name {
    margin-top: 38px;
    margin-bottom: 60px;
  }

  .menu-project-name > span {
    font-size: 20px;
    font-weight: bold;
    color: white;
  }

  .main {
    height: 100%;
  }

  .el-menu-demo {
    height: 100%;
    width: 220px;
    position: absolute;
    box-shadow: 1px 0 8px #23103c;
    -moz-box-shadow: 1px 0 8px #23103c;
    -webkit-box-shadow: 1px 0 8px #23103c;
    -o-box-shadow: 1px 0 8px #23103c;

  }

  .router-view {
    width: 85%;
    height: 88%;
    padding: 50px 10px 10px 10px;
    position: absolute;
    left: 250px;
    top: 20px;
  }

  .tab-style {
    box-shadow: 0 0 5px 5px #ddd;
    -moz-box-shadow: 0 0 5px 5px #ddd;
    -webkit-box-shadow: 0 0 5px 5px #ddd;
    -o-box-shadow: 0 0 5px 5px #ddd;
  }

  .tab-header-name > span {
    font-size: 26px;
    border-bottom: 1px solid #ddd;
    padding-bottom: 5px;
    margin-left: 10px;
  }

  .dialog-footer {
    padding: 10px 20px 20px;
    text-align: right;
    box-sizing: border-box;
  }

  .detail-input {
    width: 450px;
  }
</style>



