import Vue from 'vue';

const findAllReportTemplate = function (criteria) {
  return new Promise(resolve => {
    const url = '/api/fcrt/reportTemplate/findAllWithPage';
    Vue.http.post(url, criteria)
    .then(function (res) {
      resolve(res.body.data);
    })
    .catch(function (err) {
      // eslint-disable-next-line
      console.error('can not find report template, error: ' + err.body.message);
      resolve([]);
    });
  });
};

const findReportTemplateById = function (templateId) {
  return new Promise(resolve => {
    const url = '/api/fcrt/reportTemplate/findById/' + templateId;
    Vue.http.get(url)
    .then(function (res) {
      resolve(res.body.data);
    })
    .catch(function (err) {
      // eslint-disable-next-line
      console.error(
        'can not find report template by id: ' + templateId + ' error: '
        + err.body.message);
      resolve([]);
    });
  });
};

const saveOrUpdateReportTemplates = function (template) {
  return new Promise(resolve => {
    const url = '/api/fcrt/reportTemplate/save';
    Vue.http.post(url, template)
    .then(function (res) {
      resolve(res.body.data);
    })
    .catch(function (err) {
      // eslint-disable-next-line
      console.error('save report template fail:' + err.body.message);
      resolve([]);
    });
  });
};

const deleteReportTemplate = function (templateId) {
  return new Promise(resolve => {
    const url = '/api/fcrt/reportTemplate/delete/' + templateId;
    Vue.http.delete(url)
    .then(function (res) {
      resolve(res.body.data);
    })
    .catch(function (err) {
      // eslint-disable-next-line
      console.error('delete report template fail:' + err.body.message);
      resolve([]);
    });
  });
};
const findReportSchedulesByTmplId = function (templateId) {
  return new Promise(resolve => {
    const url = '/api/fcrt/reportSchedule/findReportSchedulesByTmplId/' + templateId;
    Vue.http.get(url)
      .then(function (res) {
        resolve(res.body.data);
      })
      .catch(function (err) {
        // eslint-disable-next-line
        console.error('can not init schedule page' + err.body.message);
        resolve([]);
      });
  });
};
export default {
  findAllReportTemplate: findAllReportTemplate,
  saveOrUpdateReportTemplates: saveOrUpdateReportTemplates,
  deleteReportTemplate: deleteReportTemplate,
  findReportTemplateById: findReportTemplateById,
  findReportSchedulesByTmplId: findReportSchedulesByTmplId,
};



