<template>
  <div>
    <el-dialog title='Report Template Detail' :visible.sync='dialogFormVisible'
               @close='resetVisible("resultReport")'>
      <el-form :model='resultReport' :rules="rules" ref="resultReport">
        <el-form-item label='Name' :label-width='formLabelWidth' prop="name">
          <el-input v-model='resultReport.name' auto-complete='off' maxlength='50'
                    class='detail-input'
                    clearable :disabled='!isEditable'></el-input>
        </el-form-item>
        <el-form-item label='Description' :label-width='formLabelWidth' prop="description">
          <el-input v-model='resultReport.description' :disabled='!isEditable' type='textarea'
                    :rows='3' class='detail-input'></el-input>
        </el-form-item>
        <el-form-item label='Scripts' :label-width='formLabelWidth'>
          <el-select v-model='selectedScriptIds' multiple collapse-tags class='selected-Script'
                     placeholder='please select script' :disabled='!isEditable'>
            <el-option
              v-for='item in allScripts'
              :key='item.id'
              :label='item.name'
              :value='item.id'>
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot='footer' class='dialog-footer'>
        <el-button @click='resetVisible("resultReport")' v-if='isEditable'>Cancel</el-button>
        <el-button type='primary' @click='confirm("resultReport")' v-if='isEditable'>
          {{isNew?'New':'Update'}}
        </el-button>
        <el-button @click='resetVisible("resultReport")' v-if='!isEditable'>Close</el-button>
      </div>
      <el-table :data="selectedScriptsTable" id='selectedScriptsTable' class="tb-edit" stripe
                border row-key="id" align="left">
        <el-table-column type="index" width="90" label="Seq#"></el-table-column>
        <el-table-column label="Name" width="180" prop="name"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="description" label="Description"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="scriptType" label="Script Type" width="105"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="sheetName" label="Sheet Name" width="180"
                         :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <el-input size="small" v-model="scope.row.sheetName" :value="scope.row.name"
                      placeholder="please input sheet name" clearable
                      :disabled="!isEditable&&!isNew"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="Old Seq#" width="90" v-if="isEditable&&!isNew"
                         prop="scriptExecuteSequence"></el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
  import ReportScriptService from '../services/ReportScriptService';
  import ReportTemplateService from '../services/ReportTemplateService';
  import Sortable from 'sortablejs';
  import _ from 'lodash';

  export default {
    name: 'ReportDetail',
    props: {
      templateId: {
        type: String,
      },
      showDetail: {
        type: Boolean,
        default: false,
      },
      isNew: {
        type: Boolean,
      },
      isEditable: {
        type: Boolean,
      },
    },
    computed: {
      selectedScriptsTable () {
        const results = [];
        this.selectedScriptIds.forEach(id => {
          const script = _.find(this.selectedScripts, function (o) {
            return o.id === id;
          });
          if (script) {
            if (!script.sheetName) {
              script.sheetName = script.name;
            }
            results.push(script);
          }
          else {
            const script = _.find(this.allScripts, function (o) {
              return o.id === id;
            });
            if (script) {
              if (!script.sheetName) {
                script.sheetName = script.name;
              }
              results.push(script);
            }
          }
        });
        if (results.length > 0) {
          this.rowDrop();
        }
        return _.sortBy(results,['scriptExecuteSequence']);
      },
    },
    data () {
      return {
        resultReport: {
          isActive: true,
        },
        selectedScripts: [],
        dialogFormVisible: false,
        formLabelWidth: '120px',
        selectedScriptIds: [],
        allScripts: [],
        sortableObj: undefined,
        rules: {
          name: [{required: true, message: 'Please Input Name', trigger: 'blur'}],
        },
      };
    },
    watch: {
      async showDetail (isShow) {
        this.dialogFormVisible = isShow;
        this.allScripts = await this.getAllScript();
      },
      async templateId (newTemplateId) {
        if (!this.isNew && newTemplateId) {
          this.resultReport = await ReportTemplateService.findReportTemplateById(newTemplateId);
          this.resultReport.isActive = this.resultReport.isActive === '1';
          const scripts = await ReportScriptService.findAllSqlByTemplateId(this.resultReport.id);
          if (scripts.reportScriptList && scripts.reportScriptList.length > 0) {
            this.selectedScripts = scripts.reportScriptList;
            this.selectedScripts.forEach(reportScript => {
              this.selectedScriptIds.push(reportScript.id);
            });
          }
          else {
            this.selectedScriptIds = [];
          }
        }
        else {
          this.resetVisible();
        }
      },
    },
    methods: {
      async confirm (resultReport) {
        this.$refs[resultReport].validate((valid) => {
          if (valid) {
            this.selectedScriptsTable.forEach((script, index) => {
              script.scriptExecuteSequence = index + 1;
            });
            this.resultReport.selectedScripts = this.selectedScriptsTable;
            this.$emit('updateReport', this.resultReport);
            this.resetVisible(resultReport);
            return true;
          }
          else {
            return false;
          }
        });
      },
      resetVisible (resultReport) {
        if (resultReport) {
          this.$refs[resultReport].clearValidate();
        }
        this.dialogFormVisible = false;
        this.$emit('updateShowState', this.dialogFormVisible);
        this.resultReport = {
          isActive: true,
        };
        this.selectedScriptIds = [];
      },
      async getAllScript () {
        return await ReportScriptService.findAllSql();
      },
      rowDrop () {
        if (this.isEditable) {
          const tbody = document.querySelector(
            '#selectedScriptsTable .el-table__body-wrapper tbody');
          const _this = this;
          this.sortableObj = Sortable.create(tbody, {
            onEnd ({newIndex, oldIndex}) {
              const currRow = _this.selectedScriptsTable.splice(oldIndex, 1)[0];
              _this.selectedScriptsTable.splice(newIndex, 0, currRow);
            },
          });
        }
        else {
          if (this.sortableObj) {
            this.sortableObj.option('disabled', !this.isEditable);
          }
        }
      },
    },
  };
</script>

<style>
  #selectedScriptsTable .el-table__body-wrapper tbody {
    cursor: pointer;
  }

  .selected-Script {
    width: 380px;
  }
</style>



