<template>
  <div class='report-template tab-style'>
    <div class='tab-header-name'>
      <span>Schedule Maintenance</span>
      <el-button size='large' type='primary' plain @click='onAddSchedule()' class='add-button'>New Schedule</el-button>
    </div>
    <el-table
      :data='AllSchedule'
      style='width: 100%;height: 90%; overflow-y: auto;background-color:transparent'
      :row-style="rowStyle"
      :header-cell-style="headerStyle"
      highlight-current-row>
      <el-table-column
        type='index'
        label='#'
        width='40'>
      </el-table-column>
      <el-table-column
        prop='name'
        label='Name'
        style='width: 5%'>
      </el-table-column>
      <el-table-column
          prop='scheduleStatus'
          label='Schedule Status'
          style='width: 5%'>
      </el-table-column>
      <el-table-column label='Last'>
        <el-table-column
          prop='lastGenerationStatus'
          label='Generate Status'
          style='width: 5%'>
        </el-table-column>
        <el-table-column
          prop='lastTriggerTimeView'
          label='Trigger Time'
          style='width: 5%'>
        </el-table-column>
        <el-table-column
          prop='lastCompletionTimeView'
          label='Completion Time'
          style='width: 5%'>
        </el-table-column>
      </el-table-column>
      <el-table-column
        label='Template'
        style='width: 5%'>
        <template slot-scope='scope'>
          <el-button size='mini' type='text' @click='viewTemplate(scope.row.templateId)'>view template</el-button>
        </template>
      </el-table-column>
      <el-table-column
        prop='scheduleType'
        label='Schedule Type'
        style='width: 5%'>
      </el-table-column>
      <el-table-column
        prop='scheduleLog'
        label='Schedule Log'
        style='width: 5%'>
        <template slot-scope='scope'>
          <el-button size='mini' type='text' @click='viewLog(scope.row.scheduleLog)'>view log</el-button>
        </template>
      </el-table-column>
      <el-table-column
        prop='description'
        label='Description'
        style='width: 5%'>
      </el-table-column>
      <el-table-column label='Action' width='315'>
        <template slot-scope='scope'>
          <el-button size='mini' type='info' plain @click='onViewSchedule(scope.row)'>View</el-button>
          <el-button size='mini' type='primary' plain @click='onUpdateSchedule(scope.row)'>Edit</el-button>
          <el-button size='mini' type='danger' plain @click='onDeleteSchedule(scope.row)'>Delete</el-button>
          <el-button size='mini' type='warning' plain v-if='scope.row.scheduleStatus != "in progress"' @click='onTriggerSchedule(scope.row)'>Trigger</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog v-bind:title='scheduleDialogTitle' width='900px' :visible.sync='scheduleVisible' :close-on-click-modal='false'>
      <el-form :model='schedule' ref='schedule' :rules='addScheduleRules' style='width: 800px'>
        <el-row v-if='!isEditable'>
          <el-col :span="12">
            <el-form-item label='Create By' label-width='130px'>
              <el-input v-model='schedule.createBy' :disabled='!isEditable'></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label='Update By' label-width='130px'>
              <el-input v-model='schedule.updateBy' :disabled='!isEditable'></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label='Name' label-width='130px' prop='name'>
              <el-input v-model='schedule.name' style='width: 300px' :disabled='!isEditable'></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label-width='130px' label='Template'>
              <el-select v-model='templateId' placeholder='select template ' :disabled='!isEditable' style='width: 275px'>
                <el-option
                  v-for='(item,index) in AllTemplate'
                  :key='item.id'
                  :label='item.name'
                  :value='item.id'>
                </el-option>
              </el-select>
              <div v-if='showTemplateAlert' class='el-form-item__error'>Please input schedule template</div>
            </el-form-item>
          </el-col>
        </el-row>

        <el-form-item label='Schedule' label-width='100px' style='width: 500px'>
          <el-radio v-model='chooseSchedule' label='0' :disabled='!isEditable'>daily</el-radio>
          <el-radio v-model='chooseSchedule' label='1' :disabled='!isEditable'>weekly</el-radio>
          <el-radio v-model='chooseSchedule' label='2' :disabled='!isEditable'>monthly</el-radio>
          <el-radio v-model='chooseSchedule' label='3' :disabled='!isEditable'>interval</el-radio>
        </el-form-item>

        <el-form-item label-width='130px'>
          <div  style="float: left" v-if='chooseSchedule == 2 && chooseSchedule != 3' >
            <el-select v-model="dayOfMonth" placeholder="dayOfMonth" :disabled='!isEditable' style='width: 130px'>
              <el-option
                v-for="item in days"
                :key="item"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
          </div>
          <div style="float: left" v-if='chooseSchedule == 1 && chooseSchedule != 3 '>
            <el-select v-model="dayOfWeek" placeholder="dayOfWeek" style='width: 120px; height: 30px' :disabled='!isEditable'>
              <el-option
                v-for="item in weeks"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
          <div v-if='showDayOfWeekAlert' class='el-form-item__error'>pls input schedule DayOfWeek</div>
          <el-input type="time" v-model='scheduleTime' :disabled='!isEditable' style='width: 130px' v-if="chooseSchedule != 3"></el-input>
          <div v-if='showDayOfMonthAlert' class='el-form-item__error'>pls input schedule DayOfMonth</div>
          <el-form-item>
            <el-input type='number' v-model="scheduleDayInterval" placeholder="inputDay" style='width: 150px' v-if='chooseSchedule == 3' :disabled='!isEditable'></el-input>
            <el-select v-model="scheduleHourInterval" placeholder="chooseHour" v-if='chooseSchedule == 3' :disabled='!isEditable' style='width: 150px'>
              <el-option
                v-for="item in hours"
                :key="item"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
            <el-select v-model="scheduleMinInterval" placeholder="chooseMin" v-if='chooseSchedule == 3' :disabled='!isEditable' style='width: 150px'>
              <el-option
                v-for="item in mins"
                :key="item"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
            <el-date-picker
              v-if='chooseSchedule == 3' :disabled='!isEditable' style='width: 200px'
              v-model="startTime"
              type="datetime"
              placeholder="startTime"
              align="right"
              :picker-options="beginAtCurrentDate">
            </el-date-picker>
            <div v-if='showStartTimeAlert' class='el-form-item__error'>pls input start time later than current time.</div>
            <div v-if='showScheduleIntervalAlert' class='el-form-item__error'>pls input schedule Interval</div>
            <div v-if='showScheduleDayIntervalAlert' class='el-form-item__error'>pls input schedule Day Interval bigger than 0</div>
          </el-form-item>
        </el-form-item>

        <el-form-item label='Email Subject' label-width='130px' prop='emailSubject'>
          <el-input v-model='schedule.emailSubject' :disabled='!isEditable' type="textarea" autosize></el-input>
        </el-form-item>
        <el-form-item label='Need Timestamp' label-width='130px'>
          <el-checkbox v-model="needTimeStamp" :disabled='!isEditable'></el-checkbox>
        </el-form-item>
        <el-form-item label='To List' label-width='130px' prop='toList'>
          <el-input v-model='schedule.toList' :disabled='!isEditable' placeholder='split by comma'></el-input>
        </el-form-item>
        <el-form-item label='CC List' label-width='130px'>
          <el-input v-model='schedule.ccList' :disabled='!isEditable' placeholder='split by comma'></el-input>
        </el-form-item>
        <el-form-item label='BCC List' label-width='130px'>
          <el-input v-model='schedule.bccList' :disabled='!isEditable' placeholder='split by comma'></el-input>
        </el-form-item>
        <el-form-item label='Email Body' label-width='130px' prop='emailBody'>
          <el-input v-model='schedule.emailBody' :disabled='!isEditable' type="textarea" autosize></el-input>
        </el-form-item>
        <el-form-item label='Description' label-width='130px'>
          <el-input v-model='schedule.description' :disabled='!isEditable' type="textarea" autosize></el-input>
        </el-form-item>
      </el-form>
      <div class='dialog-footer'>
        <el-button @click='onCancelAddSchedule' v-if='editDialog'>Cancel</el-button>
        <el-button type='primary' @click='onSubmitSchedule("schedule")' v-if='editDialog'>Submit</el-button>
        <el-button type='primary' @click='exitViewSchedule' v-if='viewDialog'>Ok</el-button>
      </div>
    </el-dialog>
    <el-dialog
        title='schedule log'
        :visible.sync='scheduleLogVisible'
        width='60%'>
      <span class="log-content">{{log}}</span>
      <span slot='footer' class='dialog-footer'>
    <el-button type='primary' @click='scheduleLogVisible = false'>OK</el-button>
  </span>
    </el-dialog>
    <Report-Detail :showDetail='showDetail' :templateId='templateId' @updateShowState='updateShowState'></Report-Detail>
  </div>
</template>

<script>
  import ScheduleScriptService from '../services/ScheduleScriptService';
  import ReportDetail from '../components/ReportDetail';
  import * as dateHelper from '../utilities/dateHelper';
  import Constants from '../utilities/constants';

  export default {
    components: {
      ReportDetail,
    },
    data () {
      return {
        AllSchedule: [],
        AllTemplate: [],
        schedule: {},
        scheduleVisible: false,
        chooseSchedule: '0',
        editDialog: false,
        viewDialog: false,
        templateVisible: true,
        showDetail: false,
        templateId: '',
        addScheduleRules: {
          name: [{required: true, message: 'Please input schedule Name', trigger: 'blur'}],
          toList: [{required: true, message: 'Please input schedule toList', trigger: 'blur'}],
          emailSubject: [{required: true, message: 'Please input schedule emailSubject', trigger: 'blur'}],
          emailBody: [{required: true, message: 'Please input schedule emailBody', trigger: 'blur'}],
        },
        showTemplateAlert: false,
        showDayOfMonthAlert: false,
        showDayOfWeekAlert: false,
        showScheduleIntervalAlert: false,
        showScheduleDayIntervalAlert: false,
        scheduleTime: '',
        isEditable: false,
        scheduleDialogTitle: '',
        scheduleDetailDisplay: false,
        weeks: [{value: 2, label: 'Monday'}, {value: 3, label: 'Tuesday'}, {value: 4, label: 'Wednesday'},
          {value: 5, label: 'Thursday'}, {value: 6, label: 'Friday'}, {value: 7, label: 'Saturday'}, {value: 1, label: 'Sunday'}],
        hours: [],
        days: [],
        mins: [],
        dayOfWeek: '',
        dayOfMonth: '',
        scheduleDayInterval: '',
        scheduleHourInterval: '',
        scheduleMinInterval: '',
        startTime: '',
        needTimeStamp: false,
        beginAtCurrentDate: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7;
          }
        },
        showStartTimeAlert: false,
        scheduleLogVisible: false,
        log: '',
      };
    },
    methods: {
      headerStyle(){
        return 'background-color:#F6F6F6;font-size:16px;text-align: center;';
      },
      rowStyle(){
        return 'background-color:transparent';
      },
      viewLog (log) {
        if (log === null) {
          log = 'no log';
        }
        this.scheduleLogVisible = true;
        this.log = log;
      },
      isInputInterval () {
        return this.scheduleDayInterval === '' && this.scheduleHourInterval === '' && this.scheduleMinInterval === '';
      },
      setScheduleDetailForm () {
        this.schedule.isActive = 1;
        this.schedule.templateId = this.templateId;
        this.schedule.scheduleDayOfWeek = this.dayOfWeek;
        this.schedule.scheduleDayOfMonth = this.dayOfMonth;
        this.schedule.scheduleHourInterval = this.scheduleHourInterval;
        this.schedule.scheduleMinInterval = this.scheduleMinInterval;
        this.schedule.scheduleDayInterval = this.scheduleDayInterval;
        this.schedule.needTimeStamp = this.needTimeStamp;
        this.schedule.startTime = dateHelper.formatDateStrToISODateStr(this.startTime);
        this.schedule.scheduleType = this.getScheduleType();

        if (this.chooseSchedule === '0') {
          this.schedule.scheduleDayOfMonth = 0;
          this.schedule.scheduleDayOfWeek = 0;
        } else if (this.chooseSchedule === '1') {
          this.schedule.scheduleDayOfMonth = 0;
        } else if (this.chooseSchedule === '2') {
          this.schedule.scheduleDayOfWeek = 0;
        }

        if (this.chooseSchedule !== '3') {
          this.schedule.scheduleDayInterval = 0;
          this.schedule.scheduleHourInterval = 0;
          this.schedule.scheduleMinInterval = 0;
          this.schedule.startTime = '';
        }
        if (this.chooseSchedule === '3') {
          this.schedule.scheduleDayOfMonth = 0;
          this.schedule.scheduleDayOfWeek = 0;
          this.schedule.scheduleHour = 0;
          this.schedule.scheduleMin = 0;
          this.schedule.scheduleDayInterval = this.schedule.scheduleDayInterval === '' ? 0: this.schedule.scheduleDayInterval;
          this.schedule.scheduleHourInterval = this.schedule.scheduleHourInterval === '' ? 0: this.schedule.scheduleHourInterval;
          this.schedule.scheduleMinInterval = this.schedule.scheduleMinInterval === '' ? 0: this.schedule.scheduleMinInterval;
        } else {
          let splitList = this.scheduleTime.split(':');
          this.schedule.scheduleHour = splitList[0];
          this.schedule.scheduleMin = splitList[1];
        }
      },
      triggerTimeView () {
        this.AllSchedule.forEach(item => {
          if (item.lastTriggerTime != null) {
            item.lastTriggerTimeView = dateHelper.getFormatDateByPattern(item.lastTriggerTime,dateHelper.ISO_DATE_FORMAT,dateHelper.UI_DATE_FORMAT);
            item.lastCompletionTimeView = dateHelper.getFormatDateByPattern(item.lastCompletionTime,dateHelper.ISO_DATE_FORMAT,dateHelper.UI_DATE_FORMAT);
          }
        });
      },
      initialList () {
        this.hours = Array.from({length:24}, (v,k) => k);
        this.days = Array.from({length:31}, (v,k) => k);
        this.mins = Array.from({length:60}, (v,k) => k);
      },
      formatTime (schedule) {
        let scheduleMin = schedule.scheduleMin;
        let scheduleHour = schedule.scheduleHour;
        scheduleMin = scheduleMin.toString().length === 1 ? '0' + scheduleMin: scheduleMin;
        scheduleHour = scheduleHour.toString().length === 1 ? '0' + scheduleHour : scheduleHour;
        this.scheduleTime = scheduleHour + ':' + scheduleMin;
      },
      updateShowState (state) {
        this.templateId = '';
        this.showDetail = state;
      },
      viewTemplate (templateId) {
        this.showDetail = true;
        this.templateId = templateId;
      },
      exitViewSchedule () {
        this.schedule = {};
        this.scheduleVisible = false;
        this.$refs.schedule.clearValidate();
      },
      onSubmitSchedule (uploadForm) {
        this.$refs[uploadForm].validate(async valid =>{
          if (this.templateId === '') {
            this.showTemplateAlert = true;
            return false;
          }
          this.showTemplateAlert = false;
          if (this.chooseSchedule === '2' && this.dayOfMonth === '') {
            this.showDayOfMonthAlert = true;
            return false;
          }
          this.showDayOfMonthAlert = false;
          if (this.chooseSchedule === '1' && this.dayOfWeek === '') {
            this.showDayOfWeekAlert = true;
            return false;
          }
          this.showDayOfWeekAlert = false;
          if (this.chooseSchedule === '3' && this.scheduleDayInterval !== '' && this.scheduleDayInterval < 0) {
            this.showScheduleDayIntervalAlert = true;
            return false;
          }
          this.showScheduleDayIntervalAlert = false;

          if (this.chooseSchedule === '3' && this.isInputInterval()) {
            this.showScheduleIntervalAlert = true;
            return false;
          }
          this.showScheduleIntervalAlert = false;
          if (this.chooseSchedule === '3' && (this.startTime === '' || new Date(this.startTime) < new Date())) {
            this.showStartTimeAlert = true;
            return false;
          }
          this.showStartTimeAlert = false;
          if (valid) {
            this.setScheduleDetailForm();
            this.addSchedule(this.schedule);
          }
          else {
            return false;
          }
        });
      },
      getScheduleType () {
        return Constants.SCHEDULE_TYPE[this.chooseSchedule];
      },
      getChooseSchedule (schedule) {
        let chooseSchedule;
        Constants.SCHEDULE_TYPE.forEach((item, index)=>{
          if (item === schedule.scheduleType) {
            chooseSchedule = index + '';
          }
        });
        return chooseSchedule;
      },
      getScheduleDetail(schedule) {
        this.dayOfWeek = schedule.scheduleDayOfWeek === 0 ? '' : schedule.scheduleDayOfWeek;
        this.dayOfMonth = schedule.scheduleDayOfMonth === 0 ? '' : schedule.scheduleDayOfMonth;
        this.scheduleHourInterval = schedule.scheduleHourInterval === 0 ? '' : schedule.scheduleHourInterval;
        this.scheduleDayInterval = schedule.scheduleDayInterval === 0 ? '' : schedule.scheduleDayInterval;
        this.scheduleMinInterval = schedule.scheduleMinInterval === 0 ? '' : schedule.scheduleMinInterval;
      },
      onCancelAddSchedule () {
        this.schedule = {};
        this.scheduleVisible = false;
        this.$refs.schedule.clearValidate();
      },
      onAddSchedule () {
        this.isEditable = true;
        this.scheduleVisible = true;
        this.editDialog = true;
        this.viewDialog = false;
        this.scheduleDialogTitle = 'Add Schedule';
        this.schedule = {};
        this.scheduleDayInterval = '';
        this.scheduleMinInterval = '';
        this.scheduleHourInterval = '';
        this.dayOfWeek = '';
        this.dayOfMonth = '';
        this.chooseSchedule = '0';
        this.templateId = '';
        this.needTimeStamp = true;
        this.scheduleTime = '01:00';
      },
      onDeleteSchedule (deleteSchedule) {
        this.deleteSchedule(deleteSchedule);
      },
      onTriggerSchedule (schedule) {
        this.triggerSchedule(schedule);
      },
      onViewSchedule  (viewSchedule) {
        this.startTime = dateHelper.getFormatDateByPattern(viewSchedule.startTime,dateHelper.ISO_DATE_FORMAT,dateHelper.UI_DATE_FORMAT);
        this.isEditable = false;
        this.schedule = viewSchedule;
        this.templateId = viewSchedule.templateId;
        this.scheduleDialogTitle = 'View Schedule';
        this.formatTime(viewSchedule);
        this.scheduleVisible = true;
        this.viewDialog = true;
        this.editDialog = false;
        this.chooseSchedule = this.getChooseSchedule(viewSchedule);
        this.getScheduleDetail(viewSchedule);
      },
      onUpdateSchedule (updateSchedule) {
        this.isEditable = true;
        this.startTime = dateHelper.getFormatDateByPattern(updateSchedule.startTime,dateHelper.ISO_DATE_FORMAT,dateHelper.UI_DATE_FORMAT);
        this.scheduleDialogTitle = 'Edit Schedule';
        this.formatTime(updateSchedule);
        this.scheduleVisible = true;
        this.viewDialog = false;
        this.editDialog = true;
        this.chooseSchedule = this.getChooseSchedule(updateSchedule);
        this.getScheduleDetail(updateSchedule);
        this.templateId = updateSchedule.templateId;

        this.schedule = Object.assign({}, updateSchedule);
      },
      async getAllTemplate () {
        this.AllTemplate = await ScheduleScriptService.findAllTemplate();
      },
      async getAllSchedule () {
        this.AllSchedule = await ScheduleScriptService.findAllSchedule();
        this.triggerTimeView();
      },
      async addSchedule (schedule) {
        this.AllSchedule = await ScheduleScriptService.addSchedule(schedule);
        this.triggerTimeView();
        this.scheduleVisible = false;
        this.schedule = {};
      },
      async deleteSchedule (deleteSchedule) {
        this.$confirm(`Are you sure to delete this schedule <strong> ${deleteSchedule.name} </strong>?`, 'Warning', {
          dangerouslyUseHTMLString: true,
          confirmButtonText: 'Delete',
          cancelButtonText: 'Cancel',
          type: 'warning',
          callback: async action => {
            if (action === 'confirm') {
              let result = await ScheduleScriptService.deleteSchedule(deleteSchedule.id);
              if (result === null) {
                return;
              }
              this.AllSchedule = result;
              this.triggerTimeView();
              this.$message({
                type: 'success',
                message: 'Delete successful.',
              });
            }
          },
        });
      },
      async triggerSchedule (triggerSchedule) {
        this.$confirm(`Are you sure to trigger this schedule <strong> ${triggerSchedule.name} </strong>?`, 'Warning', {
          dangerouslyUseHTMLString: true,
          confirmButtonText: 'trigger',
          cancelButtonText: 'Cancel',
          type: 'warning',
          callback: async action => {
            if (action === 'confirm') {
              let result = await ScheduleScriptService.triggerSchedule(triggerSchedule.id);
              if (!result) {
                this.$message({
                  type: 'fail',
                  message: 'Trigger fail.',
                });
                return;
              }
              this.$message({
                type: 'success',
                message: 'Trigger successful.',
              });
            }
          },
        });
      },
    },
    mounted () {
      this.getAllSchedule();
      this.getAllTemplate();
      this.initialList();
    },
  };
</script>

<style scoped type="text/css">
  .log-content {
    white-space: pre-wrap;
    word-wrap: break-word;
    word-break: break-all;
  }
</style>


