import Vue from 'vue';

const findAllSql = function () {
  return new Promise(resolve => {
    const url = '/api/fcrt/reportScript/findAll';
    Vue.http.get(url)
      .then(function (res) {
        resolve(res.body.data);
      })
      .catch(function (err) {
        // eslint-disable-next-line
        console.error('can not init sqls page' + err.body.message);
        resolve([]);
      });
  });
};

const findAllSqlByTemplateId = function (templateId) {
  return new Promise(resolve => {
    const url = '/api/fcrt/reportTmplScriptAssn/findScriptDetail/' + templateId;
    Vue.http.get(url)
      .then(function (res) {
        resolve(res.body.data);
      })
      .catch(function (err) {
        // eslint-disable-next-line
        console.error('can not find sqls by template id' + err.body.message);
        resolve([]);
      });
  });
};

const getRequestFormWithCondition = function (page) {
  return new Promise(resolve => {
    const url = '/api/fcrt/reportScript/findAllWithConditionQuery';
    Vue.http.post(url, page)
      .then(function (res) {
        resolve(res.body.data);
      })
      .catch(function (err) {
        // eslint-disable-next-line
        console.error('can not init sqls page' + err.body.message);
        resolve([]);
      });
  });
};

const deleteSql = function (sql) {
  return new Promise(resolve => {
    const url = '/api/fcrt/reportScript/delete/' + sql.id;
    Vue.http.delete(url)
      .then(function (res) {
        resolve(res.body.data);
      })
      .catch(function (err) {
        // eslint-disable-next-line
        console.error('delete sql fail' + err.body.message);
        resolve([]);
      });
  });
};

const fingTmplByScriptDetailId = function (sql) {
  return new Promise(resolve => {
    const url = '/api/fcrt/reportTmplScriptAssn/findTemplateReport/' + sql.id;
    Vue.http.get(url)
      .then(function (res) {
        resolve(res.body.data);
      })
      .catch(function (err) {
        // eslint-disable-next-line
        console.error(err.body.message);
        resolve([]);
      });
  });
};

const upload = function (sql) {
  return new Promise(resolve => {
    const url = '/api/fcrt/reportScript/save';
    Vue.http.post(url, sql)
      .then(function (res) {
        resolve(res.body.data);
      })
      .catch(function (err) {
        // eslint-disable-next-line
        console.error('upload sql fail:' + err.body.message);
        resolve([]);
      });
  });
};

export default {
  findAllSql: findAllSql,
  deleteSql: deleteSql,
  upload: upload,
  getRequestFormWithCondition: getRequestFormWithCondition,
  findAllSqlByTemplateId: findAllSqlByTemplateId,
  fingTmplByScriptDetailId: fingTmplByScriptDetailId,
};



